body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(left ,black ,pink);
  background: linear-gradient(to right ,black ,pink); /* Standard syntax */
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* #### Generated By: http://www.cufonfonts.com #### */

@font-face {
    font-family: 'EA Font v1.5 by Ghettoshark';
    font-style: normal;
    font-weight: normal;
    src: local('EA Font v1.5 by Ghettoshark'), url(../../static/media/EASPORTS15.650adadd.woff) format('woff');
    }

h1 {
    /* Must have the ea sports woff file in src to access it */
    font-family: 'EA Font v1.5 by Ghettoshark';
    font-weight: 600;

}
